
























import { Component, Prop, Vue } from 'vue-property-decorator';
import CircleRipple from './circleRipple.vue';

@Component({
  name: 'lb-ripple',
  components: {
    CircleRipple,
  },
})
export default class extends Vue {
  @Prop({ type: Boolean, default: true }) private centerRipple!: boolean;
  @Prop({
    default() {
      return {
        height: '40px',
        width: '40px',
        position: 'absolute',
        top: '-9px',
        left: '-11px',
        overflow: 'hidden',
      };
    },
  }) private styles!: object;
  @Prop({ default: '' }) private color!: string;
  @Prop() private opacity!: number;
  @Prop({ type: Boolean, default: false }) private disabled!: boolean;

  private nextKey: number = 0;
  private ignoreNextMouseDown: boolean = false;
  private ripples: any[] = [];
  private startTime: any = '';
  private handleMove: any = null;
  private firstTouchY: number = 0;
  private firstTouchX: number = 0;


  public mounted() {
    this.ignoreNextMouseDown = false; //  touch  mouse
  }

  private handleClick() {
    this.$emit('click');
  }
  private start(event: any, isRippleTouchGenerated: any) {
    //  touchstart  mousedown 
    if (this.ignoreNextMouseDown && !isRippleTouchGenerated) {
      this.ignoreNextMouseDown = false;
      return;
    }
    //  
    this.ripples.push({
      key: this.nextKey++,
      color: this.color,
      opacity: this.opacity,
      backgroundColor: this.centerRipple ? {} : this.getRippleStyle(event), // 
    });
    this.ignoreNextMouseDown = isRippleTouchGenerated;
  }
  private end() {
    if (this.ripples.length === 0) { return; }
    this.ripples.splice(0, 1); // 
    this.stopListeningForScrollAbort(); //  touch 
  }
  private handleMouseDown(event: any) {
    // 
    if (event.button === 0 && !this.disabled) {
      this.start(event, false);
    }
  }
  private handleTouchStart(event: any) {
    event.stopPropagation(); // 
    if (event.touches  && !this.disabled) {
      this.startListeningForScrollAbort(event); //  touchmove 
      this.startTime = Date.now();
    }
    this.start(event.touches[0], true);
  }
  // touchmove 
  private stopListeningForScrollAbort() {
    if (!this.handleMove) { this.handleMove = this.handleTouchMove.bind(this); }
    document.body.removeEventListener('touchmove', this.handleMove, false);
  }
  private startListeningForScrollAbort(event: any) {
    this.firstTouchY = event.touches[0].clientY;
    this.firstTouchX = event.touches[0].clientX;
    document.body.addEventListener('touchmove', this.handleMove, false);
  }
  private handleTouchMove(event: any) {
    const timeSinceStart = Math.abs(Date.now() - this.startTime);
    if (timeSinceStart > 300) {
      this.stopListeningForScrollAbort();
      return;
    }
    const deltaY = Math.abs(event.touches[0].clientY - this.firstTouchY);
    const deltaX = Math.abs(event.touches[0].clientX - this.firstTouchX);
    //  > 6px 
    if (deltaY > 6 || deltaX > 6) { this.end(); }
  }
  private getRippleStyle(event: any) {
    const holder = this.$refs.holder;
    //  lefttoprightbottom
    // @ts-ignore
    const rect = holder.getBoundingClientRect();
    // 
    let x = event.offsetX;
    let y;
    if (x !== undefined) {
      y = event.offsetY;
    } else {
      x = event.clientX - rect.left;
      y = event.clientY - rect.top;
    }
    // 
    let max;
    if (rect.width === rect.height) {
      max = rect.width * 1.412;
    } else {
      max = Math.sqrt(
        (rect.width * rect.width) + (rect.height * rect.height),
      );
    }
    const dim = (max * 2) + 'px';
    return {
      "width": dim,
      "height": dim,
      // margin
      'margin-left': -max + x + 'px',
      'margin-top': -max + y + 'px',
    };
  }
}
